#sideNull{
    display: none;
}

#sideCon{ 
    width: 100vw;
    height: 100vh;
    position: absolute;
    height: -webkit-fit-available;
    display: flex;
}

    #sideCon0{
        flex: 5;
        max-width: 250px;
        display: flex;
        flex-direction: column;
        background-color: darkorange;
    }

        #sideCon0Child0{
            width: 100%;
            height: 50px
        }

        #sideCon0Child1{
            flex: 1;
            position: relative;
        }

            .sideCon0Child1{
                width: 100%;
                height: 100%;
                position: absolute;
            }

        #sideCon0Child2{
            flex: 5;
            width: 100%;
        }

        #sideCon0Child3{
            width: 100%;
            height: 50px;
        }

    #sideCon1{
        flex: 1;
        position: relative;
        background-color: black;
        opacity: 50%;
    }

        .sideCon1{
            width: 100%;
            height: 100%;
            position: absolute;
            opacity: 0%;
        }