html,body{
    width: 100vw;
    height: 100vh;
    height: -webkit-fill-available;
    display: flex;
    padding: 0;
    margin: 0;
}

#root{
    flex: 1;
    display: flex;
}

img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

button{
    padding: 0;
    margin: 0;
    background-color: darkorange;
}

a{
    text-decoration: none;
    color: black;
}