#homeCon{
    flex: 1;
    display: flex;
    flex-direction: column;
}

    #homeCon0{
        height: 50px;
        display: flex;
        background-color: darkgoldenrod;
    }

        #homeCon0Child0{
            flex: 1;
            display: flex;
            position: relative;
        }

            .homeCon0Child0{
                flex: 1;
            }

            .homeCon0Child0Child0{
                width: 100%;
                height: 100%;
                position: absolute;
                opacity: 0%;
            }

        #homeCon0Child1{
            flex: 5;
        }

        #homeCon0Child2{
            flex: 1;
        }

    #homeCon1{
        flex: 10;
        display: flex;
        flex-wrap: wrap;
        overflow: scroll;
        background-color: darkorange;
    }

    #homeCon2{
        height: 50px;
        display: flex;
        background-color: darkgoldenrod;
    }

        #homeCon2Child0{
            flex: 1;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            background-color: darkgoldenrod;
        }

            .homeCon2Child0{
                width: 55%;
                height: 55%;
                object-fit: contain;
            }

        #homeCon2Child1{
            flex: 1;
        }

        #homeCon2Child2{
            flex: 1;
        }

        #homeCon2Child3{
            flex: 1;
        }